.footer {
    background-color: #1B4332;
    color: white;
    padding-top: 45px;
    padding-bottom: 45px;
    text-align: center;
}

.main {
    min-height: 90vh;
}

.notification {
    text-align: left;
    position: fixed;
    background-color: white;
    width: 280px;
    right: 20px;
    top: 20px;
    padding: 14px;
    border-radius: 8px;
    transition: .5s opacity;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.notification i {
    float: right;
    cursor: pointer;
    opacity: 0.8;
    transition: .4s opacity;
}

.notification i:hover {
    opacity: 1;
}

.not_found {
    width: 80vw;
    margin: auto;
    margin-top: 60px;
    margin-bottom: 60px;
    text-align: center;
}

.not_found img {
    width: 70%;
}

.loading h2 {
    text-align: center;
    margin-top: 60px;
    margin-bottom: 60px;
}


@media(max-width: 800px) {
    .not_found img {
        width: 100%;
    }

    .not_found {
        width: 95vw;
    }
}