.superhero {
    text-align: center;
}

.superhero .section-a {
    height: 130px;
    background-color: #1B4332;
    width: 100vw;
}

.superhero .image {
    width: 130px;
    height: 140px;
    border-radius: 100%;
    margin-top: -75px;
    cursor: pointer;
    transition: .3s filter;
}

.superhero .container-a p {
    font-size: 25px;
}

.container-a .save_btn {
    transition: .3s filter;
    width: 0px;
    position: absolute;
    filter: brightness(0%);
    z-index: 50;
    margin-top: -100px;
    cursor: pointer;
    margin-left: -30px;
}

.container-b {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 80vw;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 60px;
}

.container-b .table {
    width: 80%;
    margin: auto;
    height: 100%;
    margin-bottom: 60px;
}

.container-b .table .header {
    text-align: center;
    padding: 8px;
    background-color: #1B4332;
    color: white;
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
}

.container-b .table .header p {
    font-weight: bold;
    font-size: 20px;
}

.container-b .table .body p {
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    padding: 8px;
    font-size: 18px;
    text-align: left;
    padding-right: 10px;
    padding-left: 10px;
    background-color: #C8E8CD;
}

.container-b .table .last {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

@media(max-width: 1000px) {
    .container-b {
        grid-template-columns: 1fr;
    }
}