.search_results {
    width: 80vw;
    margin: auto;
    padding-top: 60px;
}

.search_results h2 {
    text-align: center;
}

.search_results .results {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 60px;
    text-align: center;
}

.search_results .results .result {
    transition: .5s transform;
    cursor: pointer;
    margin-bottom: 70px;
}

.search_results .results .result:hover {
    transform: scale(1.1);
}

.search_results .results p {
    font-size: 19px;
    background-color: #1B4332;
    width: 70%;
    color: white;
    margin: auto;
    padding-top: 15px;
    padding-bottom: 14px;
    margin-top: -1px;
    text-align: center;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.search_results .results img {
    width: 70%;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    margin-bottom: -4px;
}

.search_results .pagination_div {
    display: flex;
    margin-bottom: 60px;
    justify-content: center;
}

.search_results .pagination_div button {
    border: 0px;
    background-color: #1B4332;
    outline: none;
    cursor: pointer;
    padding-left: 5px;
    padding-right: 5px;
}

.search_results .pagination_div button a {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.search_results .pagination_div p {
    color: white;
    background-color: #1B4332;
    padding: 12px;
    border-left: 1px solid white;
    border-right: 1px solid white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media(max-width: 700px) {
    .search_results .results .result {
        margin-bottom: 40px;
    }

    .search_results .results {
        grid-template-columns: 1fr 1fr;
    }

    .search_results .pagination_div {
        margin-top: 30px;
    }
}

@media(max-width: 1000px) {
    .search_results .results {
        grid-template-columns: 1fr 1fr;
    }
}

@media(max-width: 500px) {
    .search_results .results {
        grid-template-columns: 1fr;
    }
} 