.dashboard {
    text-align: center;
}

.dashboard .section-a {
    width: 100vw;
    height: 130px;
    background-color: #1B4332;
}

.dashboard .user_icon {
    width: 120px;
    height: 120px;
    margin-top: -60px;
}

.dashboard .username {
    font-size: 25px;
}

.dashboard .section-b {
    display: flex;
    width: 70vw;
    margin: auto;
    margin-top: 30px;
    padding-bottom: 60px;
}

.dashboard .section-b .buttons {
    width: 250px;
    display: flex;
    flex-direction: column;
}

.dashboard .section-b .buttons a button {
    width: 100%;
}

.dashboard .section-b .buttons button {
    padding: 8px;
    margin-bottom: 10px;
    border-radius: 8px;
    border: 0px;
    color: white;
    background-color: #40916C;
    cursor: pointer;
    font-size: 17px;
    transition: .4s background;
    outline: none;
    font-weight: bold;
}

.dashboard .section-b .buttons .active {
    background-color: #1B4332;
}

.dashboard .section-b .content {
    flex: 1;
    background-color: #C8E8CD;
    margin-left: 30px;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.dashboard .settings {
    text-align: left;
}

.dashboard .settings .email {
    display: flex;
    width: 100%;
    margin-bottom: 20px;
    overflow: hidden;
    overflow-x: auto;
}

.dashboard .settings .email i {
    padding: 14px;
    font-size: 20px;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
}

.dashboard .settings .email p {
    padding: 8px;
    flex: 1;
    border: 1px solid black;
    border-left: 0px;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
}

.dashboard .settings .settings_buttons {
    display: flex;
}

.dashboard .settings .settings_buttons button {
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 8px;
    border: 0px;
    color: white;
    background-color: #40916C;
    cursor: pointer;
    font-size: 17px;
    transition: .4s background;
    outline: none; 
    width: 200px;
    font-weight: bold;
    transition: .4s background;
}

.dashboard .settings .settings_buttons button:hover {
    background-color: #1B4332;
}

.dashboard .settings .settings_buttons .second {
    margin-left: 10px;
}

@media(max-width: 1100px) {
    .dashboard .section-b {
        flex-direction: column;
    }

    .dashboard .section-b .buttons {
        width: 100%;
        margin-bottom: 30px;
    }

    .dashboard .section-b .content {
        margin-left: 0px;
    }
}

@media(max-width: 470px) {
    .dashboard .section-b {
        width: 85vw;
    }

    .dashboard .settings .settings_buttons {
        flex-direction: column;
    }

    .dashboard .settings .settings_buttons button {
        width: 100%;
    }

    .dashboard .settings .settings_buttons .second {
        margin-left: 0px;
    }
}