.home .section-a {
    background-color: #1B4332;
    width: 80vw;
    padding-left: 10vw;
    padding-right: 10vw;
    padding-top: 40px;
    padding-bottom: 40px;
    display: flex;
}

.home .section-a div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
    text-align: center;
}

.home .section-a .left {
    flex: 1;
    margin-right: 10vw;
}

.home .section-a form {
    display: flex;
    margin-top: 10px;
}

.home .section-a input {
    flex: 1;
    padding: 8px;
    border: 1px solid #D8F3DC;
    background-color: #D8F3DC;
    outline: none;
    font-size: 20px;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
}

.home .section-a button {
    width: 50px;
    border: 0px;
    outline: none;
    background-color: #40916C;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    cursor: pointer;
}

.home .section-a button img {
    width: 36px;
    margin-left: 7px;
}

.home .section-a .slide_image {
    width: 300px;
    margin: auto;
    cursor: pointer;
}

.home .section-b {
    width: 80vw;
    margin: auto;
    padding-top: 60px;
    padding-bottom: 60px;
} 

.home .section-b h2 {
    text-align: center;
}

.home .section-b .popular_searches {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 60px;
    text-align: center;
}

.home .section-b .popular_searches .popular_search {
    transition: .5s transform;
    cursor: pointer;
}

.home .section-b .popular_searches .popular_search:hover {
    transform: scale(1.1);
}

.home .section-b .popular_searches p {
    font-size: 19px;
    background-color: #1B4332;
    width: 70%;
    color: white;
    margin: auto;
    padding-top: 15px;
    padding-bottom: 14px;
    margin-top: -1px;
    text-align: center;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.home .section-b .popular_searches img {
    width: 70%;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    margin-bottom: -4px;
    height: 100%;
}


@media(max-width: 985px) {
    .home .section-b .popular_searches {
        grid-template-columns: 1fr 1fr;
    }

    .home .section-b .popular_searches .popular_search {
        margin-bottom: 40px;
    }


    .home .section-a .right {
        display: none;
    }

    .home .section-a .left {
        padding-top: 60px;
        padding-bottom: 60px;
        margin-right: 0px;
    }
}

.home .move_superman {
    animation: 2s moveSuperman forwards;
}

@media(max-width: 500px) {
    .home .section-b .popular_searches {
        grid-template-columns: 1fr;
    }
}

@keyframes moveSuperman {
    50% {transform: scale(1.4);}
    100% {transform: scale(1);}
}