.collection {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.collection .superheroInfo {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px;
}

.collection .superheroInfo img {
    width: 200px;
    height: 260px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    cursor: pointer;
    transition: .4s filter;
}

.collection .superheroInfo p {
    width: 176px;
    font-size: 19px;
    margin: auto;
    padding: 12px;
    color: white;
    margin-top: -4px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    background-color: #1B4332;
}

.collection .superheroInfo .removeBtn {
    width: 100px;
    position: absolute;
    margin-top: -175px;
    margin-left: -50px;
    padding: 8px;
    border-radius: 8px;
    background-color: #40916C;
    color: white;
    border: 0px;
    outline: none;
    font-size: 16px;
    cursor: pointer;
}

@media(max-width: 770px) {
    .collection {
        grid-template-columns: 1fr;
    }
}