.navbar {
    width: 100vw;
    background-color: #1B4332;
    padding-top: 14px;
    padding-bottom: 14px;
    display: flex;
    height: 80px;
    position: sticky;
}

.navbar .right {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    padding-right: 10vw;
}

.navbar .right a {
    color: white;
    text-decoration: none;
    padding: 10px;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.navbar .left {
    padding-left: 10vw;
}


.navbar img {
    width: 80px;
}

.navbar .mobile {
    display: none;
}

.navbar .mobile .options {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.navbar .mobile .line {
    width: 32px;
    height: 4px;
    background-color: white;
    margin: 3px 0;
}

.navbar .sidebar {
    position: absolute;
    z-index: 1000;
    top: 108px;
    width: 100vw;
    background-color: #40916C;
    flex-direction: column;
    border-top: 1px solid white;
    display: none;
    transition: .5s opacity;
    opacity: 0;
}

.navbar .sidebar a {
    padding: 14px;
    color: white;
    font-size: 20px;
    padding-left: 10vw;
    padding-right: 10vw;
    text-decoration: none;
    border-bottom: 1px solid white;
    transition: .3s background;
}

.navbar .sidebar a:hover {
    background-color: black;
}

@media(max-width: 500px) {
    .navbar .mobile {
        display: flex;
    }

    .navbar .web {
        display: none;
    }
}
  