.sign_up_container {
    display: flex;
    height: 100vh;
    width: 100vw;
}

.sign_up_container .right_container {
    width: 400px;
    height: 100vh;
    color: white;
    background-color: #1B4332;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.sign_up_container .right_container h2 {
    font-size: 26px;
    margin-bottom: 8px;
}

.sign_up_container .right_container button {
    border: 0px;
    cursor: pointer;
    font-size: 18px;
    padding: 8px;
    width: 180px;
    border-radius: 8px;
    background-color: #40916C;
    outline: none;
    color: white;
}

.sign_up_container .main_container {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    height: 100vh;
    align-items: center;
}

.sign_up_container .main_container h2 {
    font-size: 26px;
}

.sign_up_container .main_container form {
    width: 350px;
    display: flex;
    flex-direction: column;
}

.sign_up_container .main_container form button,.sign_up_container .main_container form a, .sign_up_container .main_container form div {
    margin-top: 12px;
}

.sign_up_container .main_container form button {
    width: 100%;
    padding: 8px;
    font-size: 18px;
    border-radius: 8px;
    background-color: #40916C;
    color: white;
    border: 0px;
    cursor: pointer;
    outline: none;
}


.sign_up_container .main_container form a {
    color: black;
    text-decoration: none;
    text-align: right;
}

.sign_up_container .main_container form div {
    width: 100%;
    display: flex;
}

.sign_up_container .main_container form div i {
    font-size: 25px;
    padding: 6px;
    width: 30px;
    text-align: center;
    border: 1px solid black;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    transition: .5s border-color, .5s color;
}

.sign_up_container .main_container form div input {
    flex: 1;
    transition: .5s border-color;
    border: 1px solid black;
    outline: none;
    background-color: transparent;
    padding: 6px;
    font-size: 18px;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    border-left: 0px;
}

@media(max-width: 950px) {
    .sign_up_container .right_container {
        display: none;
    }
}

@media(min-width: 950px) {
    .sign_up_container .main_container a {
        display: none;
    }
}

@media(max-width: 500px) {
    .sign_up_container .main_container form {
        width: 80vw;
    }
}